import React, { Component } from 'react';
import { Link } from 'react-router';

/* Recommend images */
import rule_1 from '../images/rules/goods-rule-pic1.jpg';
import rule_2 from '../images/rules/goods-rule-pic2.jpg';
import rule_3 from '../images/rules/goods-rule-pic3.jpg';

class Home extends Component {
  state = {
    loading: true,
    catagories: []
  }

  componentWillMount() {
    fetch('/data/categories.json')
      .then(res => res.json())
      .then(data => {
        this.setState({
          catagories: data.response,
          loading: false
        });
      })
      .catch(err => console.error(err));
  }

  render() {
    const { catagories, loading } = this.state;

    return(
      <div className="content-inner home">
        <section className="hero">
          <div className="hero__blackout"></div>
          <div className="hero__content">
            <h1 className="hero__title">   ,      </h1>
            <p className="hero__subtitle">
              Vabol -    <br/>  - .
            </p>
          </div>
        </section>

        <section className="products-list">
          <div className="container">
            <h2 className="products-list__title">Vabol </h2>

            <div className="products-promo">
              <div className="products-promo__content">
                <span className="products-promo__title">
                       !
                </span>
              </div>
            </div>

            <div className="products-list__inner">
              <div className="row">
                { catagories.map((category, index) => {
                  return(
                    <div key={index} className="product-item col-md-3">
                      <Link to={'/catalog/' + category._id} className="product-item__link">
                        <img className="img-responsive" src={'/uploads/category/' + category.thumb} alt={category.name} />
                        <div className="product-item__info">
                          <span className="product-item__title">{category.name}</span>
                        </div>
                      </Link>
                    </div>
                  );
                }) }
              </div>
            </div>

            <div className="products-list__more">
              <Link to="/catalog" className="button button-default button-big"> </Link>
            </div>
          </div>
        </section>

        <section className="start-selling">
          <div className="start-selling__blackout"></div>
          <div className="container">
            <div className="start-selling__content">
              <h2 className="start-selling__title">   <Link to="/" className="start-selling__link">Vabol</Link></h2>
              <p className="start-selling__subtitle">    .  ,     .</p>
              <Link to="/new" className="button button-inverted button-big"> </Link>
            </div>
          </div>
        </section>

        <section className="product-rules">
          <div className="container">
            <h2 className="product-rules__title">      Vabol?</h2>

            <div className="product-features">
              <div className="product-features__item row">
                <div className="product-features__img-holder col-xs-4 col-xs-offset-1">
                  <img className="product-features__img" src={rule_1} alt="rule_1"/>
                </div>
                <div className="product-features__content-holder col-xs-5">
                  <div className="product-features__content">
                    <h3 className="product-features__title"> </h3>
                    <p className="product-features__text">          </p>
                  </div>
                </div>
              </div>

              <div className="product-features__item row">
                <div className="product-features__content-holder col-xs-5 col-xs-offset-3">
                  <div className="product-features__content">
                    <h3 className="product-features__title"> </h3>
                    <p className="product-features__text"> <strong>Vabol</strong>      ,    ( ,   )</p>
                  </div>
                </div>
                <div className="product-features__img-holder col-xs-4">
                  <img className="product-features__img" src={rule_2} alt="rule_2"/>
                </div>
              </div>

              <div className="product-features__item row">
                <div className="product-features__img-holder col-xs-4 col-xs-offset-1">
                  <img className="product-features__img" src={rule_3} alt="rule_3"/>
                </div>
                <div className="product-features__content-holder col-xs-5">
                  <div className="product-features__content">
                    <h3 className="product-features__title"> </h3>
                    <p className="product-features__text">  ,   .    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section className="benefits">
          <div className="benefits__blackout"></div>
          <div className="container">
            <div className="row">
              <div className="col-md-3"></div>
              <div className="col-md-3"></div>
              <div className="col-md-3"></div>
              <div className="col-md-3"></div>
            </div>
          </div>
        </section>

        <section className="creative-potential">
          <div className="container">
            <div className="creative-potential__content">
              <h2 className="creative-potential__title">    ?</h2>
              <p className="creative-potential__subtitle">    !</p>
              <Link to="/new" className="button button-default button-big"> </Link>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default Home;


