import React from 'react';

function filter() {
  return(
    <div className="filter">

      <div className="filter-group">
        <div className="filter-title">
          <strong></strong>
        </div>
        <div>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r1" />
            <span className="filter-item__text" > 100 .</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r1"/>
            <span className="filter-item__text" > 100 .  500 .</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r1"/>
            <span className="filter-item__text" > 500 .  1000 .</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r1"/>
            <span className="filter-item__text" > 1000 .</span>
          </label>
        </div>
      </div>

      <hr/>
      
      <div className="filter-group">
        <div className="filter-title">
          <strong> </strong>
        </div>
        <div>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r2" />
            <span className="filter-item__text" > </span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r2"/>
            <span className="filter-item__text" > </span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r2"/>
            <span className="filter-item__text" > </span>
          </label>
        </div>
      </div>

      <hr/>

      <div className="filter-group">
        <div className="filter-title">
          <strong> </strong>
        </div>
        <div>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" ></span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" ></span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" ></span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" ></span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" ></span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" ></span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" ></span>
          </label>
        </div>
      </div>

    </div>
  );
}

export default filter;


